<i18n>
{
  "en": {
    "iealert": "<p> You use <b>Internet Explorer</b>. We can't ensure that this browser support all of the technologies used on this page. </p> <p> We recommend using <b> Chrome </ b>, <b> Opera </ b>, <b> Firefox </ b> or <b> Safari </ b>. </ p>"
  },
  "fr": {
	"iealert": "<p> Vous utilisez <b>Internet Explorer</b>. Nous ne pouvons assurer que ce navigateur supporte toutes les technologies utilisées sur cette page. </p> <p> Nous recommandons d'utiliser <b> Chrome </ b>, <b> Opera </ b>, <b> Firefox </ b> ou <b> Safari </ b>. </ p>"
  }
}
</i18n>

<template>
<span class="aeris-browser-alert-host" v-show="false">
 <div class="browser-alert">
        <div class="browser-alert-content">
          <i class="fa fa-exclamation fa-2x"></i>
		  <span v-html="message"></span>	          
        </div>
        <i class="fa fa-times" v-on:click.stop="hide"></i>
        <button class="browser-alert-button" type="button" v-on:click.stop="doNotShow">Ne plus afficher</button>
      </div>
 </span>
</template>

<script>
export default {
  props: {
  	lang:  {
      type: String,
      default: 'en'
    }
  },
  
  data () {
    return {
      message:'',
      visible: false
    }
  },
  
  mounted() {
	  	this.$i18n.locale = this.lang
        var cooki = this.getCookie('browser-alert');
        var isCompatible = true;
        var isIE = /*@cc_on!@*/false || !!document.documentMode;
		
      
        if (isIE) {
        	isCompatible = false;
        	this.message = this.$i18n.t("iealert")
        }	else {
        	this.message= "Coucocu"
        	isCompatible = false
        }
        if(!cooki && !isCompatible) {
        	this.visible = true	;
        }
      },
  
  computed: {
  },
  
  methods: {
      
      hide: function() {
        this.visible = false;
      },
      
      doNotShow: function() {
        this.hide();
        setCookie('browser-alert', 1, 365);
      },
      
      setCookie: function(cname, cvalue, exdays) {
      var d = new Date();
      d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
      var expires = 'expires=' + d.toUTCString();
      document.cookie = cname + '=' + cvalue + ';' + expires + ';path=/';
    },

    getCookie: function(cname) {
      var name = cname + '=';
      var ca = document.cookie.split(';');
      for(var i = 0; i < ca.length; i++) {
        var c = ca[i];
        while (c.charAt(0) === ' ') {
          c = c.substring(1);
        }

        if (c.indexOf(name) === 0) {
          return c.substring(name.length, c.length);
        }

      }

      return '';
    }
  }
}
</script>

<style>
	   .aeris-browser-alert-host {
        display: block;
    }

	   .browser-alert {
        display: flex;
        flex-flow: column nowrap;
        padding: 10px 10px 10px 20px;
        color: #333;
        background-color: #F5B041
    }
    .browser-alert .fa-times {
        top: 10px;
        right: 10px
    }
    .browser-alert .fa-times:hover {
        align-self: flex-start;
        cursor: pointer;
        opacity: 0.6
    }
    .browser-alert-content {
        display: flex;
        align-items: center;
        flex-flow: row nowrap;
        margin-right: 10px
    }
    .browser-alert-content .fa {
        margin-right: 5px
    }
    .browser-alert-content p {
        margin: 0 10px
    }
    .browser-alert-content a {
        color: inherit
    }
    .browser-alert-button {
        align-self: flex-end;
        margin: 10px 30px 0 0;
        color: #333;
        background-color: transparent;
        outline: none;
        border: 1px solid
    }
    .browser-alert-button:hover {
        opacity: 0.6;
        cursor: pointer
    }
</style>


